import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../../helpers';
import { ScriptLoaderService } from '../../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../../layouts/toast-notification/toast.component';
import { GlobalValidate } from '../../../../../../_services/validate.service';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./configure-service.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class ConfigServiceComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
@ViewChild(ToastComponent) toast: ToastComponent;
 
configserviceForm: FormGroup;
configservice= new ConfigServiceData();

public status ="";
msg = [];
	 
bootstrapFromValues = [];

constructor( private globalValid: GlobalValidate, private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
		this.status = "Update";
		this.populateData();
	}
	
    ngOnInit() {
		this.configserviceForm = new FormGroup({			
			'configserviceTicket' : new FormControl(''),
			'configServiceContract' : new FormControl(''),
			'configserviceTicketCat' : new FormControl(''),
			'configServiceJobcardType' : new FormControl(''),
			'configServiceJobcardCat' : new FormControl(''),
			'configServiceJobcardRefno' : new FormControl(''),
			'configServiceContractRefno' : new FormControl(''),
			'configserviceTicketRefno' : new FormControl(''),
			'configserviceJobcardRefno' : new FormControl(''),
			'configServiceJobcardSerialnoMandatory' : new FormControl(''),
			'configticketNewEmailEnable' : new FormControl(''),
			'configticketNewSMSEnable' : new FormControl(''),
			'configticketFollowupEmailEnable' : new FormControl(''),
			'configticketFollowupSMSEnable' : new FormControl(''),
			'configticketClosedEmailEnable' : new FormControl(''),
			'configticketClosedSMSEnable' : new FormControl(''),
			'configJcNewEmailEnable' : new FormControl(''),
			'configJcNewSmsEnable' : new FormControl(''),
			'configJcReadyEmailEnable' : new FormControl(''),
			'configJcReadySmsEnable' : new FormControl(''),
			'configJcDeliveredEmailEnable' : new FormControl(''),
			'configJcDeliveredSmsEnable' : new FormControl('')
		});
	}
	  
	   	get configserviceTicket(){ return this.configserviceForm.get('configserviceTicket')};
	 	get configServiceContract(){ return this.configserviceForm.get('configServiceContract')};
	    get configserviceTicketCat(){ return this.configserviceForm.get('configserviceTicketCat')};
	    get configServiceJobcardType(){ return this.configserviceForm.get('configServiceJobcardType')};
	    get configServiceJobcardCat(){ return this.configserviceForm.get('configserviceJobcardCat')};
 		get configServiceJobcardRefno(){ return this.configserviceForm.get('configServiceJobcardRefno')};
	 	get configServiceContractRefno(){ return this.configserviceForm.get('configServiceContractRefno')};
	    get configserviceTicketRefno(){ return this.configserviceForm.get('configserviceTicketRefno')};
	    get configserviceJobcardRefno(){ return this.configserviceForm.get('configserviceJobcardRefno')};
	    get configServiceJobcardSerialnoMandatory(){ return this.configserviceForm.get('configServiceJobcardSerialnoMandatory')};
 		get configticketNewEmailEnable(){ return this.configserviceForm.get('configticketNewEmailEnable')};
	 	get configticketNewSMSEnable(){ return this.configserviceForm.get('configticketNewSMSEnable')};
	    get configticketFollowupEmailEnable(){ return this.configserviceForm.get('configticketFollowupEmailEnable')};
	    get configticketFollowupSMSEnable(){ return this.configserviceForm.get('configticketFollowupSMSEnable')};
	    get configticketClosedEmailEnable(){ return this.configserviceForm.get('configticketClosedEmailEnable')};
 		get configticketClosedSMSEnable(){ return this.configserviceForm.get('configticketClosedSMSEnable')};
	 	get configJcNewEmailEnable(){ return this.configserviceForm.get('configJcNewEmailEnable')};
	    get configJcNewSmsEnable(){ return this.configserviceForm.get('configJcNewSmsEnable')};
	    get configJcReadyEmailEnable(){ return this.configserviceForm.get('configJcReadyEmailEnable')};
	    get configJcReadySmsEnable(){ return this.configserviceForm.get('configJcReadySmsEnable')};
	  	get configJcDeliveredEmailEnable(){ return this.configserviceForm.get('configJcDeliveredEmailEnable')};
	    get configJcDeliveredSmsEnable(){ return this.configserviceForm.get('configJcDeliveredSmsEnable')};

	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<ConfigServiceData>(sessionStorage.getItem('requestUrl')+'/axelacrm/service/config-service-data ',JSON.stringify({configId:'1' }),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.configservice = data['populateData'][0];
			  															},
															 (err) => {
																	console.log("ERROR: "+err);
															 });
	 }
	 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['portal/manager']);      
		 }

 		getCheckValues(temp:string){
			return this.globalValid.getCheckValue(temp);
		}
 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.configservice[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.configserviceForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});

				this.configservice['requestType'] = 'update';
				this.configservice['configId'] = '1';

				this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/service/config-service',
									JSON.stringify(this.configservice),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
				  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.configserviceForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.configservice);
	  }
 
	
}

	
	
export class ConfigServiceData {
	
	configserviceTicket: string;
	configServiceContract: string;
	configserviceTicketCat: string;
	configServiceJobcardType: string;
	configServiceJobcardCat: string;
	configServiceJobcardRefno: string;
	configServiceContractRefno: string;
	configserviceTicketRefno: string;
	configserviceJobcardRefno: string;
	configServiceJobcardSerialnoMandatory: string;
	configticketNewEmailEnable: string;
	configticketNewSMSEnable: string;
	configticketFollowupEmailEnable: string;
	configticketFollowupSMSEnable: string;
	configticketClosedEmailEnable: string;
	configticketClosedSMSEnable: string;
	configJcNewEmailEnable: string;
	configJcNewSmsEnable: string;
	configJcReadyEmailEnable: string;
	configJcReadySmsEnable: string;
	configJcDeliveredEmailEnable: string;
	configJcDeliveredSmsEnable: string;
}





